<template>
  <div>
    <quill-editor
      v-model="contentValue"
      ref="myQuillEditor"
      :options="editorOption"
      :disabled="!isEdit"
      @blur="onEditorBlur($event)"
      @focus="onEditorFocus($event)"
      @ready="onEditorReady($event)"
      @change="updateContents($event)">
    </quill-editor>
    <!--    accept="image/*"-->
    <input :id="editorId" style="display: none;" type="file" @change="beforeUpload($event)"/>
    <!-- -->
    <!--    <el-upload class="avatar-uploader" action name="img" :show-file-list="false" :before-upload="beforeUpload">-->
    <!--    </el-upload>-->
  </div>
</template>

<script>
import api from "@/scripts/api";
import { Quill, quillEditor } from 'vue-quill-editor'
// import VueQuillEditor from "vue-quill-editor";
import ImageResize from 'quill-image-resize-module';
// import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import { ModulePicture } from "@/scripts/dictionary";
import { transformValue } from "@/scripts/utils";

Quill.register('modules/imageResize', ImageResize);

export default {
  name: "RQuillEditor",
  components: { "quill-editor": quillEditor },
  props: {
    isEdit: {
      // 
      type: Boolean,
      default: true
    },
    funType: {
      type: Number,
      default: ModulePicture.StudentNote
    },
    content: {
      type: String,
      default: ""
    },
    maxHeight: {
      type: String,
      default: ""
    }
    // routeType: {
    //   type: String,
    //   default: "course"
    // }
  },
  data () {
    return {
      editorId: "upload" + new Date().getTime(),
      // show: false,
      fileId: "",
      maxFileNum: 3,
      imgBase: "",
      contentValue: transformValue("show", this.content, this.getPicture)
    };
  },
  computed: {
    editorOption () {
      if (this.isEdit) {
        return {
          placeholder: "",
          theme: "snow", // or 'bubble'
          modules: {
            toolbar: {
              container: [
                ["bold"], //    , "italic", "underline"
                [{ header: [1, 2, 3, 4, 5, 6, false] }], // 12 
                [{ list: "ordered" }, { list: "bullet" }], // 
                [{ indent: "-1" }, { indent: "+1" }], // 
                [{ color: [] }, { background: [] }], // 
                // [{ font: [] }], // 
                // ["blockquote", "code-block"], //   
                [{ align: [] }], // 
                // [{ list: "ordered" }, { list: "bullet" }], // 
                ["image"] // , "photo"
              ],
              handlers: {
                image: value => {
                  if (value) {
                    if (this.editorPictureNum() < this.maxFileNum) {
                      // sendToAPP(FuncType.albumOpen, "");
                      document.getElementById(this.editorId).click();
                      // // input
                      // document.querySelector('.avatar-uploader input').click()
                    } else {
                      this.$message({
                        type: 'warning',
                        message: ""
                      });
                    }
                    // document.querySelector("#quill-upload input").click();
                  } else {
                    this.quill.format("image", false);
                  }
                },
                photo: value => {
                  if (value) {
                    if (this.editorPictureNum() < this.maxFileNum) {
                      // sendToAPP(FuncType.cameraOpen, "");
                      // document.getElementById("upload-input").click();
                    } else {
                      this.$message({
                        type: 'warning',
                        message: ""
                      });
                    }
                    // document.querySelector("#quill-upload input").click();
                  } else {
                    this.quill.format("photo", false);
                  }
                }
              }
            },
            imageResize: {
              displayStyles: {
                backgroundColor: 'black',
                border: 'none',
                color: 'white'
              },
              modules: ['Resize', 'DisplaySize', 'Toolbar']
            }
          }
        };
      } else {
        return {
          placeholder: "",
          theme: "bubble", // or 'bubble'
          modules: {
            toolbar: {
              container: []
            }
          }
        };
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      // window.uploadImage = imgFile => {
      //   if (this.routeType === "history") {
      //     this.outClassUpload(imgFile);
      //   } else {
      //     this.afterRead(imgFile);
      //   }
      // };
      document.getElementsByClassName("ql-editor")[0].style.maxHeight = this.maxHeight;
    });
  },
  methods: {
    getPicture (id, type) {
      return `${this.$root.PictureURL}?fileName=${id}&fileType=${type}&platFormType=1`;
    },
    onEditorBlur (editor) {
      // console.log('editor blur!', editor)
    },
    onEditorFocus (editor) {
      // console.log('editor focus!', editor)
    },
    onEditorReady (editor) {
      // console.log('editor ready!', editor)
    },
    customButtonClick () {
      // alert(
      //   `You can custom the button and listen click event to do something...\n...`
      // );
    },
    updateContents (e) {
      // 200500
      e.quill.deleteText(this.funType === ModulePicture.StudentNote ? 500 : 200, 4);
      const value = transformValue("save", e.html, this.getPicture);
      this.$emit("change", value);
    },
    // //  showsave
    // transformValue(type, value) {
    //   return transformValue("show", value, this.routeType === 'history' ? this.$mainHttp.resourse.picture : this.$gatewayHttp.resourse.picture);
    // },

    // 
    editorPictureNum () {
      let count = 0;
      this.contentValue.replace(
        /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
        (match, capture) => {
          count++;
        }
      );
      return count;
    },
    beforeUpload (event) {
      const file = event.target.files && event.target.files.length > 0 ? event.target.files[0] : null;
      // let file = event;
      document.getElementById(this.editorId).value = "";
      if (file.type && file.type !== "image/jpeg" && file.type !== "image/png" && file.type !== "image/jpg") {
        this.$message({
          showClose: true,
          message: " jpg png ",
          type: "error"
        });
        return false;
      } else {
        const hasType = file.name && file.name.split(".").length > 1;
        const falseType = file.name && file.name.split(".").length > 1 && ["jpeg", "jpg", "png"].indexOf(file.name.split(".")[1]) === -1;
        if (hasType && falseType) {
          this.$message({
            showClose: true,
            message: " jpg png ",
            type: "error"
          });
        } else if (!hasType) {
          this.$message({
            showClose: true,
            message: "",
            type: "error"
          });
        }
      }
      if (file.size > 1024 * 1024 * 20) {
        this.$message({
          showClose: true,
          message: "20MB",
          type: "error"
        });
        return false;
      }

      // html5
      const reader = new FileReader();
      // BASE64
      reader.readAsDataURL(file);
      // 
      reader.onload = (e) => {
        const imgFile = e.target.result;
        // if (this.routeType === "history") {
        const data = new FormData();
        data.append("file", file);
        data.append("type", this.funType);
        api.Common.InteractUploadFile(data).then(res => {
          if (res.Result) {
            this.imgBase = imgFile;
            this.fileId = res.fileId;
            this.insertImage();
          } else {
            this.$message.error("");
          }
        });
        // } else {
        //   this.afterRead(imgFile);
        // }
      };
    },
    // afterRead (file) {
    //   // console.log("", file);
    //   // 20M
    //   if (file.length > 1024 * 1024 * 20) {
    //     this.$message({
    //       type: 'warning',
    //       message: "20M"
    //     });
    //     return false;
    //   }
    //   if (file) {
    //     const reqData = {
    //       funType: this.funType,
    //       imgBaseString: file
    //     };
    //     this.$gatewayHttp.resourse.upload(reqData).then(res => {
    //       if (res.Result) {
    //         this.imgBase = file;
    //         this.fileId = res.Data;
    //
    //         this.insertImage();
    //       } else {
    //         this.$message.error("");
    //       }
    //     }).catch(err => {
    //       console.log("err:", err);
    //     });
    //   }
    // },
    // outClassUpload (file) {
    //   const data = new FormData();
    //   data.append("file", dataURLtoFile(file, ""));
    //   data.append("type", this.funType);
    //
    //   this.$mainHttp.resourse.upload(data).then(res => {
    //     if (res.Result) {
    //       this.imgBase = file;
    //       this.fileId = res.fileId;
    //       this.insertImage();
    //     } else {
    //       this.$message.error("");
    //     }
    //   });
    // },
    insertImage () {
      // res
      // 
      const quill = this.$refs.myQuillEditor.quill;
      // 
      if (this.fileId) {
        // 
        const length = quill.getSelection() ? quill.getSelection().index : 0;
        const filePath = this.getPicture(this.fileId, this.funType);

        //   filePath
        quill.insertEmbed(length, "image", filePath);
        // length = quill.getSelection() ? quill.getSelection().index : 0;
        // 
        quill.setSelection(length + 1);
      }
    },
    // change
    // 
    initValue (value) {
      this.contentValue = transformValue("show", value, this.getPicture);
    }
  },
  watch: {
    maxHeight (newVal, oldVal) {
      document.getElementsByClassName("ql-editor")[0].style.maxHeight = newVal;
    }
    /* *
    *  
    * */
    // content(newVal, oldVal) {
    //   if (!newVal) {
    //     this.contentValue = ""
    //   } else if (newVal !== oldVal) {
    //     this.contentValue = this.transformValue("show", newVal);
    //   }
    // }
  }
};
</script>

